import React from 'react';

import { Admin, Resource } from 'react-admin';
import jsonServerProvider from 'ra-data-json-server'

// Language
import polyglotI18nProvider from 'ra-i18n-polyglot';
import vietnameseMessages from 'ra-language-vietnamese';

// Icon
import PostIcon from '@material-ui/icons/Book';
import UserIcon from '@material-ui/icons/Group';

// Components
import { UserList } from './user/user'
import { PostList, PostEdit, PostCreate } from './post/Post';
import Dashboard from './dashbroad/Dashbroad';
import authProvider from './auth/authProvider';
import Layout from '../layout/Layout';
import customRoutes from '../route'

// -------Main------
const i18nProvider = polyglotI18nProvider(() => vietnameseMessages, 'vi')

const dataProvider = jsonServerProvider('https://jsonplaceholder.typicode.com');
const App = () => (
  <Admin
    authProvider={authProvider}
    dashboard={Dashboard}
    dataProvider={dataProvider}
    i18nProvider={i18nProvider}
    layout={Layout}
    customRoutes={customRoutes}
  >
    <Resource name="posts" list={PostList} edit={PostEdit} create={PostCreate} icon={PostIcon} />
    <Resource name="users" list={UserList} icon={UserIcon} />

  </Admin>
);

export default App;
