import * as React from "react";
import { List, SimpleList, Datagrid, TextField, ReferenceField, EditButton } from 'react-admin';
import { Filter, Edit, Create, SimpleForm, TextInput, ReferenceInput, SelectInput } from 'react-admin'
import { useMediaQuery } from '@material-ui/core';

// Post Edit
const PostTitle = ({ record }) => {
    return <span>Post {record ? `"${record.title}"` : ''}</span>;
};

const PostEdit = props => (
    <Edit title={<PostTitle />} {...props}>
        <SimpleForm>
            <TextInput disabled source="id" />
            <ReferenceInput source="userId" reference="users">

                <SelectInput optionText="name" />
            </ReferenceInput>

            <TextInput source="title" />

            <TextInput multiline source="body" />
        </SimpleForm>
    </Edit>
);

// Post Create
const PostCreate = props => (
    <Create {...props}>
        <SimpleForm>
            <ReferenceInput source="userId" reference="users">
                <SelectInput optionText="name" />
            </ReferenceInput>
            <TextInput source="title" />
            <TextInput multiline source="body" />
        </SimpleForm>
    </Create>
)

// Post Filter
const PostFilter = (props) => (
    <Filter {...props}>
        <TextInput label="Tìm kiếm" source="q" alwaysOn />
        <ReferenceInput label="User" source="userId" reference="user" allowEmpty>
            <SelectInput optionText="name" />
        </ReferenceInput>
    </Filter>
)

// Post List
const PostList = props => {

    const isSmall = useMediaQuery(theme => theme.breakpoints.down('sm'));

    return (
        <List
            filters={<PostFilter />} 
            title="Danh sách bài viết"
            {...props} 
        >
            {isSmall ? (
                <SimpleList
                    primaryText={record => record.title}
                    secondaryText={record => `${record.views} views`}
                    tertiaryText={record => new Date(record.published_at).toLocaleDateString()}
                />
            ) : (
                    <Datagrid>
                        <TextField source="id" />
                        <ReferenceField label="User" source="userId" reference="users">
                            <TextField source="name" />
                        </ReferenceField>
                        <TextField source="title" />
                        <TextField source="body" />
                        <EditButton />
                    </Datagrid>
                )}
        </List>
    )
};

export { PostEdit, PostList, PostCreate, PostTitle, PostFilter };