import React, { useState } from 'react';
import Card from '@material-ui/core/Card';
import CardContent from '@material-ui/core/CardContent';
import Button from '@material-ui/core/Button';
import { Title } from 'react-admin';
import { makeStyles } from '@material-ui/core/styles';
import { createMuiTheme } from '@material-ui/core/styles';

const useStyles = makeStyles({
    label: { width: '10em', display: 'inline-block' },
    button: { margin: '1em' },
});


const Configuration = () => {
    const classes = useStyles();
    const [type, setType] = useState('light')
    const theme = createMuiTheme({
        palette: {
            type: type, // Switching the dark mode on is a single property value change.
        },
    });


    return (
        <Card>
            <Title title={('Cấu hình')} />
            <CardContent>
                <div className={classes.label}>
                    {('Màu nền')}
                </div>
                <Button
                    variant="contained"
                    className={classes.button}
                    color={theme.palette.type === 'light' ? 'primary' : 'default'}
                    onClick={() => setType('light')}
                >
                    {(`Sáng `)}
                </Button>
                <Button
                    variant="contained"
                    className={classes.button}
                    color={theme.palette.type === 'dark' ? 'primary' : 'default'}
                    onClick={() => setType('dark')}
                >
                    {('Tối')}
                </Button>
            </CardContent>
        </Card>
    );
};

export default Configuration;